Date.prototype.format = function (f) {
  if (!this.valueOf()) return ' ';

  var weekKorName = [
    '',
    '',
    '',
    '',
    '',
    '',
    ''
  ];

  var weekKorShortName = ['', '', '', '', '', '', ''];

  var weekEngName = [
    'Sunday',
    'Monday',
    'Tuesday',
    'Wednesday',
    'Thursday',
    'Friday',
    'Saturday'
  ];

  var weekEngShortName = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

  var d = this;
  var h = this;

  return f.replace(/(yyyy|yy|MM|dd|KS|KL|ES|EL|HH|hh|mm|ss|a\/p)/gi, function (
    $1
  ) {
    switch ($1) {
      case 'yyyy':
        return d.getFullYear(); //  (4)

      case 'yy':
        return (d.getFullYear() % 1000).zf(2); //  (2)

      case 'MM':
        return (d.getMonth() + 1).zf(2); //  (2)

      case 'dd':
        return d.getDate().zf(2); //  (2)

      case 'KS':
        return weekKorShortName[d.getDay()]; //  ( )

      case 'KL':
        return weekKorName[d.getDay()]; //  ( )

      case 'ES':
        return weekEngShortName[d.getDay()]; //  ( )

      case 'EL':
        return weekEngName[d.getDay()]; //  ( )

      case 'HH':
        return d.getHours().zf(2); //  (24 , 2)

      case 'hh':
        return ((h = d.getHours() % 12) ? h : 12).zf(2); //  (12 , 2)

      case 'mm':
        return d.getMinutes().zf(2); //  (2)

      case 'ss':
        return d.getSeconds().zf(2); //  (2)

      case 'a/p':
        return d.getHours() < 12 ? '' : ''; // / 

      default:
        return $1;
    }
  });
};

String.prototype.string = function (len) {
  var s = '',
    i = 0;
  while (i++ < len) {
    s += this;
  }
  return s;
};

String.prototype.zf = function (len) {
  return '0'.string(len - this.length) + this;
};

Number.prototype.zf = function (len) {
  return this.toString().zf(len);
};

export default Date;
