import { Border, Button, Input, Text } from 'Components/Atoms';
import React, { useState } from 'react';

import { List } from 'Components/Molecules';
import { auctionApi } from 'Apis/auctionApi';
import beforeBid from 'assets/img/beforeBid.png';
import bidComplete from 'assets/img/bidComplete.png';
import { comma } from 'Utils/comma-utils';
import styled from 'styled-components';
import { useInput } from 'Hooks/useInput';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
`;

const Bidding = ({ data, userInfo, isOpen }) => {
  const [value, setValue] = useInput({});
  const [productPrice, setProductPrice] = useState('');
  const [complete, setIsComplete] = useState(!isOpen);

  const onChange = e => {
    e.preventDefault();
    const { name, value } = e.target;
    const price = value;
    const formattingPrice = price.replace(/[^0-9]/g, '');
    setValue(name, formattingPrice);
    setProductPrice(`${comma(formattingPrice)}`);
  };

  const biddingHandler = async () => {
    if (parseInt(value.bidPrice) <= parseInt(data.currentPrice)) {
      alert('    .');
      return;
    }

    const res = await auctionApi.registerBid(userInfo.id, data.url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        AUTH_TOKEN: userInfo.accessToken
      },
      body: JSON.stringify({
        bidPrice: value.bidPrice,
        auctionId: data.id
      })
    });

    // const err = await res.json();
    // if (err.message == 'Continuous bidding is not possible.') {
    //   alert('        . ');
    //   return;
    // }
    setIsComplete(true);
  };

  return !complete ? (
    <List alignCenter={true}>
      <Text></Text>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          width: '90%',
          borderRadius: 5,
          border: `1px solid #EAEAEA`,
          marginBottom: 20
        }}
      >
        <Input
          readOnly
          style={{ textAlign: 'center', flex: 6, border: 'none', margin: 0 }}
          value={`${comma(data.currentPrice)}`}
          name="startingPrice"
        ></Input>
        <div
          style={{
            flex: 1,
            textAlign: 'center'
          }}
        >
          
        </div>
      </div>

      <Text> </Text>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          width: '90%',
          borderRadius: 5,
          border: `1px solid #6E44FF`,
          marginBottom: 40
        }}
      >
        <Input
          style={{ textAlign: 'center', flex: 6, border: 'none', margin: 0 }}
          value={comma(productPrice)}
          name="bidPrice"
          placeholder={`${comma(data.currentPrice)}   `}
          onChange={onChange}
        ></Input>
        <div
          style={{
            flex: 1,
            textAlign: 'center'
          }}
        >
          
        </div>
      </div>

      <div
        style={{
          width: '100%',
          backgroundColor: '#F5F5F7',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          padding: '10px 10px',
          marginBottom: '40px',
          flexDirection: 'column'
        }}
      >
        <div style={{ width: '90%', fontSize: 14 }}>
          {`-        . \n`}
        </div>
        <div style={{ width: '90%', fontSize: 14 }}>
          {`-        \n`}
        </div>
      </div>

      <Button
        onClick={biddingHandler}
        primary
        common
        style={{ position: 'relative' }}
      >
        <img
          src={beforeBid}
          style={{
            position: 'absolute',
            top: -37,
            left: '25%',
            width: '100%',
            maxWidth: 200
          }}
        />
         GO!
      </Button>
    </List>
  ) : (
    <Container
      style={{
        height: '100%'
      }}
    >
      {/* <div
        style={{
          width: '100%',
          display: 'flex',
          justifyContent: 'center',
          padding: 10,
          fontSize: 25,
          color: '#6E44FF'
        }}
      >
        *()*
      </div> */}
      <img src={bidComplete} style={{ width: '40%' }}></img>
      <div
        style={{
          width: '100%',
          display: 'flex',
          justifyContent: 'center',
          marginTop: 10,
          fontSize: 20
        }}
      >
         .
      </div>
    </Container>
  );
};

export default Bidding;
